import React, { useEffect, useState } from "react";
import { CSVLink } from "react-csv";

const headers = [
  { label: "", key: "address" },
  { label: " ", key: "store_name" },
  { label: " ", key: "category_kor" },
  { label: " ", key: "open_service" },
  { label: " ", key: "detailed_classification" },
  { label: "", key: "authorization_date" },
  { label: "", key: "closed_date" },
  { label: " ", key: "state" }
];

const RealTimeDownLoadCsv = (props) => {
  const [data, setData] = useState([]);
  useEffect(() => {
    setListHandler();
  }, [props.data_list]);

  const setListHandler = async () => {
    let dataList = [];
    await props.data_list.forEach((info) => {
      dataList.push({
        address: info.address,
        store_name: info.store_name,
        category_kor: info.category_kor,
        open_service: info.open_service,
        detailed_classification: info.detailed_classification,
        authorization_date: info.authorization_date,
        closed_date: info.closed_date,
        state: info.state
      });
    });
    setData(dataList);
  };
  return (
    <>
      <CSVLink
        data={data}
        headers={headers}
        filename={`${props.city_name}_${props.date_kor}_${props.openOrClose}.csv`}
      >
        
      </CSVLink>
    </>
  );
};

export default RealTimeDownLoadCsv;
